.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
span.earth{
  animation: planet-spin infinite 2s reverse;
}
span.earth::before{
  height: 20px;
  width: 20px;
  content: "";
  border-radius: 50%;
  background-color: blue;
  box-shadow: inset black 1px 3px 4px 0px;
  position: absolute;
  top: -40%;

}

span.earth::after{
  height: 100px;
    width: 100px;
    content: "";
    border: 1px solid #a90808;
    border-radius: 50%;
    background-color: #a90808;
    box-shadow: black -1px -1px 7px 0px;
    position: absolute;
    top: 25%;
    left: 25%;

}

.Logo-Container{
  border-radius: 50% 50%;
  height: 40vmin;
  width: 40vmin;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes planet-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
